*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --accent-color: #d41443;
  --secondary-accent-color: #ffa5ba;
  --primare-bg-color: #fff;
  --secondary-bg-color: #fff5f6;
  --third-bg-color: #c2e297;
  --forth-bg-color: #f0d1a5;
  --card-set-gap: 30px;
  --section-set-gap: 94px;
  --primary-text-color: #907e82;
  --accent-text-color: #000;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --time-duration: 250ms;
}

h1, h2, h3, p {
  margin: 0;
}

body {
  color: var(--primary-text-color);
  background-color: var(--primare-bg-color);
  letter-spacing: .04em;
  font-family: DM Sans, sans-serif;
}

a {
  cursor: pointer;
  text-decoration: none;
  display: block;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

header {
  display: none;
}

.header-mob {
  width: 100%;
  background-color: #ffb8ca;
}

.menu-open-btn {
  width: 20px;
  height: 12px;
  background-color: #ffb8ca;
  border: 0;
  margin-left: auto;
  padding-top: 18px;
  padding-bottom: 20px;
  display: block;
}

.mob-menu {
  background-color: var(--header-color);
  z-index: 2;
  width: 100vw;
  height: 100vh;
  padding: 60px 20px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.menu-close-btn {
  width: 14px;
  height: 14px;
  background-color: #e17992;
  border: 0;
  align-items: center;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.menu-close-btn__icon-svg {
  width: 100%;
  height: 100%;
}

.mob-menu .container {
  height: 100%;
  background-color: #e17992;
  flex-direction: column;
  display: flex;
}

.mob-menu__item:not(:last-child) {
  margin-bottom: 10px;
}

.mob-menu__wrap {
  width: 250px;
  height: 210px;
  background: repeating-linear-gradient(#e18298 1px, rgba(0, 0, 0, 0) 2px, rgba(0, 0, 0, 0) 42px);
}

.mob-menu__link {
  color: #fff;
  padding-top: 13px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu__link:hover, .mob-menu__link:focus {
  color: #d41443;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.mob-menu-btn {
  color: #d41443;
  cursor: pointer;
  max-width: 209px;
  height: 44px;
  background-color: var(--primare-bg-color);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.mob-menu-btn__icon {
  margin-left: 15px;
}

.hero {
  height: 502px;
  background-color: #ffb8ca;
  position: relative;
  overflow: hidden;
}

.hero--center {
  z-index: 1;
  margin-top: 194px;
  position: relative;
}

.hero--title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  width: 218px;
  height: 106px;
  margin-bottom: 20px;
  font-family: Titan One;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
}

.hero__list {
  display: flex;
}

.hero__item:nth-child(2) {
  margin-left: 8px;
}

.hero__title--label {
  text-transform: uppercase;
  color: #d41443;
  font-family: Titan One;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: .95;
}

.hero__btn {
  text-align: center;
  border-radius: 20px;
  align-items: center;
  font-family: DM Sans;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
  display: flex;
}

.hero__btn--made {
  color: #d41443;
  background: #fff;
  border: none;
  padding: 8px 14px;
}

.hero__btn--product {
  color: #fff;
  background: #d41443;
  border: none;
  padding: 8px 14px;
}

.hero--circle {
  width: 346px;
  height: 344px;
  background-color: #ffa5ba;
  border: none;
  border-radius: 50%;
  padding: 0;
  position: absolute;
  top: 83px;
  transform: translateX(5%);
}

.hero__img {
  position: absolute;
  top: 9px;
  left: 81px;
}

.products {
  background: var(--primare-bg-color);
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 60px;
}

.products__slogan {
  text-transform: uppercase;
  letter-spacing: .04em;
  text-align: center;
  color: var(--secondary-accent-color);
  margin-top: 0;
  margin-bottom: 15px;
  padding-top: 0;
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.16667;
}

.products__title {
  text-transform: uppercase;
  letter-spacing: .04em;
  text-align: center;
  color: var(--accent-color);
  margin-top: 0;
  margin-bottom: 8px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.14706;
}

.products__list {
  width: 100%;
  height: auto;
  margin: 0 auto;
}

.products__item {
  width: 100%;
  height: 100%;
  background-color: var(--forth-bg-color);
  border-radius: 24px;
  display: block;
  position: relative;
}

.products__item:first-child {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  margin-bottom: -140px;
}

.products__item:first-child:after {
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
}

.products__item:nth-child(2) {
  background-color: var(--secondary-accent-color);
}

.products__item:nth-child(3) {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  margin-bottom: -140px;
}

.products__item:nth-child(3):after {
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
}

.products__item:nth-child(5) {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  margin-bottom: -140px;
}

.products__item:nth-child(5):after {
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  margin-bottom: -140px;
  position: absolute;
}

.products__item:nth-child(6) {
  background-color: var(--third-bg-color);
}

.products__picture {
  background-color: rgba(0, 0, 0, 0);
}

.products__subtitle {
  text-transform: uppercase;
  letter-spacing: .04em;
  text-align: center;
  color: var(--primare-bg-color);
  align-items: flex-end;
  margin: 0 auto;
  padding-top: 160px;
  font-family: Titan One;
  font-size: 20px;
  line-height: 1.15;
}

.products__box {
  width: 100%;
  justify-content: center;
  gap: 4px;
  margin-top: 32px;
  display: flex;
}

.products__box:after {
  content: "";
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

.products__icon {
  fill: var(--primare-bg-color);
  border-color: #000;
  border-radius: 50%;
}

.products__text {
  letter-spacing: .04em;
  text-align: center;
  color: var(--primare-bg-color);
  align-content: flex-end;
  margin-top: 32px;
  margin-bottom: 32px;
  padding-left: 41px;
  padding-right: 39px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
}

.products__holder {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-bottom: 40px;
  display: flex;
}

.products__pic {
  background-color: var(--primare-bg-color);
  fill: var(--accent-color);
  text-align: center;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.about {
  background-color: var(--secondary-bg-color);
  padding-top: 60px;
}

.about__pretitle {
  letter-spacing: .04em;
  text-align: center;
  color: var(--secondary-accent-color);
  text-transform: uppercase;
  margin-bottom: 15px;
  font-family: Titan One, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16667;
}

.about__title {
  letter-spacing: .04em;
  text-align: center;
  color: var(--accent-color);
  text-transform: uppercase;
  margin-bottom: 32px;
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14706;
}

.about__background {
  width: 100%;
  height: calc(100vw - 40px);
  background-image: url("about-img-mob.2263ac99.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 40px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .about__background {
    background-image: url("about-img-mob@2x.3a16ad05.png");
  }
}

@media screen and (min-width: 320px) {
  .about__background {
    height: 279px;
  }
}

.about__text-wrap {
  background-image: url("about-bg-mob.1bda211f.png");
  background-position-y: 71px;
  background-repeat: no-repeat;
  background-size: contain;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.58333;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .about__text-wrap {
    background-image: url("about-bg-mob@2x.77e919d0.png");
  }
}

.about__main-text {
  max-width: 220px;
  color: var(--accent-text-color);
  margin-bottom: 32px;
}

.about__additional-text {
  max-width: 220px;
  color: var(--primary-text-color);
  margin-bottom: 20px;
}

.about__button {
  height: 44px;
  text-align: center;
  color: var(--accent-color);
  background-color: var(--primare-bg-color);
  border: none;
  border-radius: 22px;
  padding-left: 37px;
  padding-right: 37px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28571;
}

.button__icon {
  width: 7px;
  height: 10px;
  fill: var(--accent-color);
  margin-left: 14px;
}

.advantage {
  background-color: var(--secondary-bg-color);
  padding-top: 40px;
}

.advantage__list {
  padding-top: 86px;
}

@media screen and (min-width: 768px) {
  .advantage__list {
    align-items: flex-start;
    padding-top: 126px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__list {
    padding-top: 121px;
  }
}

.advantage__item {
  flex-shrink: 1;
  position: relative;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .advantage__item {
    width: 180px;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__item {
    width: 268px;
  }
}

@media screen and (max-width: 767px) {
  .advantage__item:not(:last-child) {
    margin-bottom: 98px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .advantage__item:not(:last-child) {
    margin-right: 4px;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__item:not(:last-child) {
    margin-right: 132px;
  }
}

.advantage__item:before {
  height: 50px;
  content: "";
  display: inline-block;
  position: absolute;
  top: -66px;
  left: 0;
}

@media screen and (min-width: 768px) {
  .advantage__item:before {
    top: -86px;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__item:before {
    top: -90px;
  }
}

