.advantage__number {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--accent-color);
  font-family: Titan One;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.1389;
}

@media screen and (min-width: 768px) {
  .advantage__number {
    font-size: 48px;
    line-height: 1.1458;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__number {
    font-size: 58px;
    line-height: 1.1379;
  }
}

.advantage__description {
  color: var(--accent-text-color);
  margin-top: 25px;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.7;
}

@media screen and (min-width: 768px) {
  .advantage__description {
    font-size: 14px;
    line-height: 1.74;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__description {
    letter-spacing: .02em;
    font-size: 16px;
    line-height: 1.76;
  }
}

@media screen and (max-width: 767px) {
  .gallery {
    background-image: linear-gradient(180deg, var(--secondary-bg-color) 42%, var(--primare-bg-color) 0);
    padding-top: 60px;
  }
}

.gallery-list {
  align-items: center;
  gap: 2.53px;
  display: flex;
}

.gallery-item:not(:last-child) .gallery-image {
  margin-bottom: 1.89px;
}

.gallery-image {
  border: rgba(0, 0, 0, 0);
  border-radius: 8px;
  transition: scale 1s;
}

.gallery-image:hover {
  transform: scale(1.5);
}

.gallery-wrap-3 {
  display: grid;
}

.gallery-wrap-5 {
  align-self: flex-start;
  margin-top: 14px;
}

.box4 {
  grid-area: -2 / 1 / -3 / 3;
}

.box5 {
  grid-area: 3 / 1 / 1 / 1;
}

.customer-reviews {
  padding-top: 60px;
}

.customer-reviews__list, .customer-reviews__item:not(:last-child) {
  margin-bottom: 16px;
}

.customer-reviews__userpic {
  margin: 0 auto 32px;
}

.customer-reviews__wrap {
  background-color: #fafafa;
  margin-bottom: 16px;
  padding: 43px 50px 26px;
  position: relative;
}

.customer-reviews__wrap:before {
  content: "";
  width: 54px;
  height: 40px;
  background-image: url("“.cd9e453b.png");
  background-size: contain;
  display: inline-block;
  position: absolute;
  top: 30px;
  left: 53px;
}

.customer-reviews__text {
  text-align: center;
  color: #000;
  background: repeating-linear-gradient(to top, rgba(0, 0, 0, 0), #e1e1e1 2px, rgba(0, 0, 0, 0) 1px, rgba(0, 0, 0, 0) 31px);
  padding-left: 15px;
  padding-right: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 2.58;
  position: relative;
}

.customer-reviews__part {
  width: 4px;
  height: 4px;
  border: 2px solid #c7133e;
  border-radius: 4px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.customer-reviews__part:before {
  content: "";
  width: 4px;
  height: 4px;
  border: 2px solid #c7133e;
  border-radius: 4px;
  position: absolute;
  top: -2px;
  left: -14px;
}

.customer-reviews__part:after {
  content: "";
  width: 4px;
  height: 4px;
  border: 2px solid #c7133e;
  border-radius: 4px;
  position: absolute;
  top: -2px;
  left: 10px;
}

.customer-reviews__user {
  text-align: center;
  color: #000;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.86;
}

.pagination__list {
  justify-content: center;
  display: flex;
}

.pagination__item:not(:last-child) {
  margin-right: 18px;
}

.pagination__btn {
  width: 12px;
  height: 12px;
  background-color: #d9d9d9;
  border-style: none;
  border-radius: 12px;
}

.pagination__btn:hover, .pagination__btn:focus {
  background-color: #d31342;
}

.pagination__btn-current {
  display: none;
}

.pagination__icon {
  cursor: pointer;
  fill: #d31342;
}

.pagination__icon:hover, .pagination__icon:focus {
  fill: #d41342;
}

.contacts {
  background-image: linear-gradient(to bottom, var(--primare-bg-color) 0 25%, #fff5f6 25% 100%);
  padding-top: 60px;
}

.contacts__list {
  margin-bottom: 32px;
}

.contacts-card {
  background-color: var(--primare-bg-color);
  border-radius: 24px;
  padding: 40px 32px;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts-card:not(:last-child) {
  margin-bottom: 20px;
}

.contacts-card__representation {
  color: var(--primare-bg-color);
  height: 16px;
  min-width: 80px;
  background: #f0d1a5;
  border-radius: 4px;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 25px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.86;
  display: inline-flex;
}

.contacts-card__representation.contacts-card__representation--green {
  background-color: #c2e297;
}

.contacts-card__location {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 24px;
}

.contacts-card__town {
  letter-spacing: .04em;
  color: var(--accent-text-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.86;
}

.contacts-card__text {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.contacts-card__work-time {
  border-bottom: 1px solid #e1e1e1;
  padding: 24px 0;
}

.contacts-card__weekdays {
  margin-bottom: 8px;
}

.contacts-card__day {
  color: var(--accent-text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.86;
}

.contacts-card__time {
  color: var(--secondary-accent-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.86;
}

.contacts-card__address {
  padding-top: 24px;
  font-style: normal;
}

.contacts-card__link {
  color: var(--primary-text-color);
  transition: color var(--time-duration) var(--timing-function);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  text-decoration: none;
}

.contacts-card__link:hover, .contacts-card__link:focus {
  color: var(--accent-color);
}

.contacts__buttons {
  text-align: center;
}

.contacts__button {
  width: 161px;
  height: 44px;
  color: var(--primare-bg-color);
  transition: background-color var(--time-duration) var(--timing-function);
  background-color: #d41342;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: inline-flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.contacts__button:hover, .contacts__button:focus {
  background-color: var(--secondary-accent-color);
}

.contacts__button.contacts__button--white {
  background-color: var(--primare-bg-color);
  color: var(--accent-color);
  box-shadow: none;
  transition: background-color var(--time-duration) var(--timing-function);
}

.contacts__button.contacts__button--white:hover, .contacts__button.contacts__button--white:focus {
  background-color: #ecebeb;
}

.contacts__button:not(:last-child) {
  margin-bottom: 10px;
}

.contacts__icon {
  width: 8px;
  height: 11px;
  fill: currentColor;
  margin-left: 12px;
}

.footer {
  height: 409px;
  background-color: #fff5f6;
  padding-top: 40px;
  padding-bottom: 3px;
}

.footer-container {
  width: 100%;
  max-width: 320px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  display: flex;
}

.footer-base {
  max-width: 232px;
  margin-bottom: 40px;
  display: block;
}

.footer-contacts {
  max-width: 236px;
  margin-bottom: 40px;
  margin-left: auto;
  display: block;
}

.footer-base_titte {
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 16px;
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
}

.footer-base_text {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #d41443;
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
}

.footer-contacts_list {
  justify-content: right;
  margin: 0;
  display: flex;
}

.footer-contacts_item {
  width: 32px;
  height: 32px;
}

.footer-contacts_item:not(:first-child) {
  margin-left: 28px;
}

.footer-contacts_soc-link {
  width: 100%;
  height: 100%;
  background-color: #ffa5ba;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-property: background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.footer-contacts_soc-link:hover, .footer-contacts_soc-link:focus {
  background-color: #d41443;
}

.footer-contacts_tel {
  text-align: right;
  margin-top: 40px;
  font-family: DM Sans;
  font-size: 11px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.83;
  display: block;
}

.color {
  color: #d41443;
}

.footer-logo {
  max-width: 150px;
  text-align: center;
  color: #907e82;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
  font-family: DM Sans;
  font-size: 14px;
